import React from 'react'
import {Input, Button} from 'antd'

const LoginDisplay = ({auth, loading, onChange, login}) => {
  return(
    <form onSubmit={login} style={{width:600, margin:"0 auto", padding:20}}>
                <h2>Inicia sesión</h2>
                <p>
                <Input 
                    name="email"
                    type="email"
                    onChange={onChange}
                    value={auth.email}
                    placeholder="Tu correo" 
                    />    
                </p> 
                <p>
                <Input 
                    name="password"
                    type="password"
                    onChange={onChange}
                    value={auth.password}
                    placeholder="Tu contraseña" 
                    />    
                </p>   
                <Button loading={loading} type="primary" htmlType="submit" >Inicia sesión</Button>
            </form>
  )
}

export default LoginDisplay