import React, {Component} from 'react'
import NewCloth from './NewCloth'
import Axios from 'axios'
import toastr from 'toastr'


class newCloth extends Component{
  state = {
    form:{},
    loading:false
  }

  onSubmit = (e) => {
    e.preventDefault()
    const {form} = this.state
    console.log(form)
    Axios.post('https://childman.herokuapp.com/admin', form)
    .then((r)=>{
      toastr.success("¡se ha creado un nuevo articulo!")
      console.log(r)})
  }

  onChange = (e) => {
    const field = e.target.name
    const value = e.target.value
    const {form} = this.state
    form[field] = value
 }

  render(){
    const {form, loading} = this.state
    return(
        <div>
          <NewCloth 
            onSubmit={this.onSubmit}
            onChange={this.onChange}
            loading = {loading}
            form={form}
          />
        </div>
    )}
}

export default newCloth